$primary: #32c3ff;

@import url('//fonts.googleapis.com/css?family=Pacifico&text=Pure');
@import url('//fonts.googleapis.com/css?family=Roboto:700&text=css');
@import url('//fonts.googleapis.com/css?family=Kaushan+Script&text=!');
@import url('//fonts.googleapis.com/css?family=Pacifico&text=Pure');
@import url('//fonts.googleapis.com/css?family=Roboto:700&text=css');
@import url('//fonts.googleapis.com/css?family=Kaushan+Script&text=!');

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.info {
  margin: 20px;
  padding: 10px 20px;
  border-radius: 10px;
  display: inline-block;
  background-color: #5776e9;
  color: white;
}

.info.info-danger {
  background-color: #e73f3f;
}

.App {
  text-align: center;

  .App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 80px;
  }

  .App-header {
    padding: 10px;
    color: #363A36;
    width: 100%;
    background-size: cover;
    height: 70px;
    border-bottom: 5px solid orange;
    padding-top: 0;

    a {
      color: #363A36;
      padding: 0 5px;
      text-decoration: unset;
    }

    a.active {
      font-weight: bold;
    }
  }

  .App-title {
    font-size: 1.5em;
  }

  .App-intro {
    font-size: large;
  }

  @keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }

    to {
      transform: rotate(360deg);
    }
  }
}

.Home {
  img {
    height: 100vh; 
    width: 100vw;
  }
}

.profile {
  .result {
    font-size: 1.2em;
    color: $primary;
  }
}

.eachBeer {
  display: flex;
  flex-wrap: wrap;
  width: 400px;
}

.eachBeer img {
  height: 200px;  
  width: 63px;
  margin: 15px;
}

.Beers {
  display: flex;
  flex-wrap: wrap;
  text-align: left;
  margin: 20px 0 20px 60px;
  padding: 10px;
  height: 50%;
}

.beerDeets {
  width: 55%;
  box-shadow: 0px 1px 2px rgba(207, 207, 207, 0.603);
  border: 1px solid #f7f7f7;
  border-radius: 5px;
  height: auto;
  padding: 10px 23px;
}

.brewSearch {
  display: flex;
  justify-content: center;
}

.search {
  width: 40%;
  border-radius: 5px;   
} 

.signup {
  margin-left: 59%;
  margin-top: 10%;
}

.sign {
  background-image: url('http://yesofcorsa.com/wp-content/uploads/2018/08/Brewery-Wallpaper-HD.jpg');
  background-size: cover;
  height: 100vh;
  width: 100vw;
}

.hoverButton {
  background-color: lightgray;
  transition: .5s;
}

.hoverButton:hover {
  background-color: orange;
  transition: .5s;
}

h2 {
  font-weight: bold;
  margin-top: 15px;
}

h3 {
  font-size: 1.5rem;
  font-weight: bold;
}
input:focus {
  outline: 5px auto orange;
}

.navbar-dark .navbar-nar .navbar-link {
  color: #363A36 !important;
}

.navbar a {
  font-size: 18px;
  font-weight: bold;
  color: #363A36;
}

.navbar a:hover {
  color: orange;
}

a.nav-item.logo {
  font-size: 24px;
}

.orange {
  color: orange;
}

.green {
  color: #29A745;
}

.white {
  color: white;
}

.white:hover {
  color: white;
}

.button:hover {
  background-color: orange;
}

a.waves-effect.waves-light {
  border: 1px solid black;
  margin-right: 50px;
  color: green;
}

.form-dark .font-small {
  font-size: 0.8rem; }

.form-dark [type="radio"] + label,
.form-dark [type="checkbox"] + label {
  font-size: 0.8rem; }

.form-dark [type="checkbox"] + label:before {
  top: 2px;
  width: 15px;
  height: 15px; }

.form-dark .md-form label {
  color: #fff; }

.form-dark input[type=text]:focus:not([readonly]) {
  border-bottom: 1px solid #00C851;
  -webkit-box-shadow: 0 1px 0 0 #00C851;
  box-shadow: 0 1px 0 0 #00C851; }

.form-dark input[type=text]:focus:not([readonly]) + label {
  color: #fff; }

.form-dark input[type=password]:focus:not([readonly]) {
  border-bottom: 1px solid #00C851;
  -webkit-box-shadow: 0 1px 0 0 #00C851;
  box-shadow: 0 1px 0 0 #00C851; }

.form-dark input[type=password]:focus:not([readonly]) + label {
  color: #fff; }

.form-dark input[type="checkbox"] + label:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 17px;
  height: 17px; 
  z-index: 0;
  border: 1.5px solid #fff;
  border-radius: 1px;
  margin-top: 2px;
  -webkit-transition: 0.2s;
  transition: 0.2s; }

.form-dark input[type="checkbox"]:checked + label:before {
  top: -4px;
  left: -3px;
  width: 12px;
  height: 22px;
  border-style: solid;
  border-width: 2px;
  border-color: transparent #00c851 #00c851 transparent;
  -webkit-transform: rotate(40deg);
  -ms-transform: rotate(40deg);
  transform: rotate(40deg);
  -webkit-backface-visibility: hidden;
  -webkit-transform-origin: 100% 100%;
  -ms-transform-origin: 100% 100%;
  transform-origin: 100% 100%; }

.spin {
  margin: 20px;
  border-radius: 5px;
  color: white;
  background-color: orange;
  width: 140px;
}

.board {
  margin-top: 50px;
}

.account {
  margin-right: 130px;
  font-size: 18px;
  font-weight: bold;
}

.favorite {
  border-radius: 5px; 
  margin: auto; 
  margin-bottom: 5px;
  background-color: orange;
  color: white;
}

.logo img {
  width: 2.5rem;
  height: 2.9rem;
  padding: 5px;
  margin-bottom: 6px;
}

.login {
  width: 1.5rem;
  height: 1.5rem;
  margin: 2.5px 5px 0 0;
}

.stage {
  height: 300px;
  width: 500px;
  margin: auto;
  margin-left: 1rem;
  position: absolute;
  top: 0; right: 0; bottom: 0; left: 0;
  perspective: 9999px;
  transform-style: preserve-3d;
}

.layer {
  width: 100%;
  height: 100%;
  position: absolute;
  transform-style: preserve-3d;
  animation: ಠ_ಠ 5s infinite alternate ease-in-out -7.5s;
  animation-fill-mode: forwards;
  transform: rotateY(40deg) rotateX(33deg) translateZ(0);
}

.layer:after {
  font: 150px/0.65 'Pacifico', 'Kaushan Script', Futura, 'Roboto', 'Trebuchet MS', Helvetica, sans-serif;
  content: 'Brew\A    tastic!';
  white-space: pre;
  text-align: center;
  height: 100%;
  width: 100%;
  position: absolute;
  top: 50px; 
  color: darken(#fff, 4%);
  letter-spacing: -2px;
  text-shadow: 4px 0 10px hsla(0, 0%, 0%, .13);
}

$i: 1;
$NUM_LAYERS: 20;
@for $i from 1 through $NUM_LAYERS {
  .layer:nth-child(#{$i}):after{
    transform: translateZ(($i - 1) * -1.5px);
  }
}

.layer:nth-child(n+#{round($NUM_LAYERS/2)}):after {
  -webkit-text-stroke: 3px hsla(0, 0%, 0%, .25);
}

.layer:nth-child(n+#{round($NUM_LAYERS/2 + 1)}):after {
  -webkit-text-stroke: 15px dodgerblue;
  text-shadow: 6px 0 6px darken(dodgerblue,35%),
               5px 5px 5px darken(dodgerblue,40%),
               0 6px 6px darken(dodgerblue,35%);
}

.layer:nth-child(n+#{round($NUM_LAYERS/2 + 2)}):after {
  -webkit-text-stroke: 15px darken(dodgerblue, 10%);
}

.layer:last-child:after {
  -webkit-text-stroke: 17px hsla(0, 0%, 0%, .1);
}

.layer:first-child:after{
  color: #fff;
  text-shadow: none;
}

@keyframes ಠ_ಠ {
  100% { transform: rotateY(-40deg) rotateX(-43deg); }
}